import React, { useState, useRef, useMemo } from 'react';
import './player.css';

import roblox from '../assets/roblox.mp3';
import siu from '../assets/siu.mp3';
import ay_mi_madre from '../assets/ay-mi-madre.mp3';
import siu2 from '../assets/siu2.mp3';
import almendra1 from '../assets/almendra1.mp3';
import almendra2 from '../assets/almendra2.mp3';
import almendra3 from '../assets/almendra3.mp3';
import es_una_mierda from '../assets/es-una-mierda.mp3';
import comunista from '../assets/comunista.mp3';

import cris from '../assets/imgs/cris.png';
import oof from '../assets/imgs/oof.jpg';
import almendra from '../assets/imgs/almendra.png';
import comunismo from '../assets/imgs/comunismo.png';
import caca from '../assets/imgs/caca.png';

export const Player = () => {

    let audios = [];
    audios.push(roblox);
    audios.push(siu);
    audios.push(ay_mi_madre);
    audios.push(siu2);
    audios.push(almendra1);
    audios.push(almendra2);
    audios.push(almendra3);
    audios.push(es_una_mierda);
    audios.push(comunista);

    let audiosObject = [];
    for (let i = 0; i < audios.length; i++) {
        if (i === 1) {
            let audioTemp = new Audio(audios[i]);
            audioTemp.volume = 0.2;
            audiosObject.push(audioTemp);
        } else if (i === 2 || i === 3) {
            let audioTemp = new Audio(audios[i]);
            audioTemp.volume = 0.7;
            audiosObject.push(audioTemp);
        } else {
            audiosObject.push(new Audio(audios[i]));
        }
    }

    const inputRefs = useMemo(() => Array(9).fill(null).map(i => React.createRef()), []);

    const playAudio = (j) => {

        for (let i = 0; i < audiosObject.length; i++) {
            audiosObject[i].currentTime = 0;
            audiosObject[i].pause();
            if (inputRefs[i].current) {
                inputRefs[i].current.classList.remove("playing");
            }
        }

        audiosObject[j].play();

        inputRefs[j].current.classList.add("playing");

        audiosObject[j].onended = function () {
            inputRefs[j].current.classList.remove("playing");
        }

    }

    return (
        <>
            <div className="mt-5">
                <h4 className="text-center raleway">Pulsa en los botones y ocurrirá la magia ✨</h4>
            </div>
            <div className="row justify-content-center mt-3 mb-2 pb-4">
                <div className="col-6 text-center p-4">
                    <button ref={inputRefs[0]} onClick={() => playAudio(0)} className="green btn-circle">
                        <img src={oof} alt="oof" />
                    </button>
                </div>
                <div className="col-6 text-center p-4">
                    <button ref={inputRefs[1]} onClick={() => playAudio(1)} className="yellow btn-circle">
                        <img src={cris} alt="cris" />
                    </button>
                </div>
                <div className="col-6 text-center p-4">
                    <button ref={inputRefs[2]} onClick={() => playAudio(2)} className="black btn-circle">
                        <img src={cris} alt="cris" />
                    </button>
                </div>
                <div className="col-6 text-center p-4">
                    <button ref={inputRefs[3]} onClick={() => playAudio(3)} className="blue btn-circle">
                        <img src={cris} alt="cris" />
                    </button>
                </div>
                <div className="col-6 text-center p-4">
                    <button ref={inputRefs[4]} onClick={() => playAudio(4)} className="light-blue btn-circle">
                        <img className="almendra" src={almendra} alt="almendra" />
                    </button>
                </div>
                <div className="col-6 text-center p-4">
                    <button ref={inputRefs[5]} onClick={() => playAudio(5)} className="fuchsia btn-circle">
                        <img className="almendra" src={almendra} alt="almendra" />
                    </button>
                </div>
                <div className="col-6 text-center p-4">
                    <button ref={inputRefs[6]} onClick={() => playAudio(6)} className="green-light btn-circle">
                        <img className="almendra" src={almendra} alt="almendra" />
                    </button>
                </div>
                <div className="col-6 text-center p-4">
                    <button ref={inputRefs[7]} onClick={() => playAudio(7)} className="white btn-circle">
                        <img className="caca" src={caca} alt="caca" />
                    </button>
                </div>
                <div className="col-6 text-center p-4">
                    <button ref={inputRefs[8]} onClick={() => playAudio(8)} className="red btn-circle">
                        <img className="comunismo" src={comunismo} alt="comunismo" />
                    </button>
                </div>
            </div>
            <div>
                <p className="text-end"><i>Creado por Christianinski Inc.</i></p>
            </div>
        </>
    )
}
